*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family:  sans-serif;
}

.main_div{
  width: 100%;
  height: 100vh;
  background: #9babc3;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.center_div{
  width: 25%;
  height: 80vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
  border-radius: 15px;
}

h1{
  color: white;
  background: transparent;
  background-color: #735499;
  padding: 9px 0 12px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);

}

input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;

}

.newbtn{
  min-height: 40px;
  width: 40px;
  border-radius: 50%;
  border-color: transparent;
  background-color: #8566aa;
  color: #fff;
  font-size: 35px;
  border: none;
  outline: none;
  margin-left: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
}



.newbtn:hover{
  background-color: #20bf6b;
  
}

ol{
  margin-top: 30px;
  /* margin-right: 50px; */

}

ol li{
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;
  /* border-bottom: 1px solid grey; */
 
  
}
.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  
}

.todo_style .fa-times {
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #8566aa;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
  /* font-size: 300px; */
}

.todo_style .fa-times:hover{
  background-color: crimson;


}

.todo_style .fa-items:hover .list_style {
  color: crimson;


}


@media(max-width: 768px){
  .center_div{
    width: 55%;

  }
  .todo_style .fa-items{
    margin: 0 15px 0 15px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .newbtn{
    margin-left: 5px;
    min-height: 40px;
    width: 40px;
    color: #fff;
    font-size: 30px;
  }
}